<template>
  <div id="particles-js"></div>
</template>
<script>
import "particles.js";
import particlasConfig from "@/assets/particles.json";
export default {
  data() {
    return {};
  },
  mounted() {
    this.$nextTick(() => {
      this.initParticlesJS();
    });
  },
  methods: {
    initParticlesJS() {
      //   particlesJS("particles-js",{particlesConfig});
      particlesJS("particles-js", {
        particles: {
          number: {
            value: 100,
            density: {
              enable: true,
              value_area: 900
            }
          },
          color: {
            value: "#ffffff"
          },
          shape: {
            type: "edge",
            stroke: {
              width: 0,
              color: "#222"
            },
            polygon: {
              nb_sides: 5
            },
            image: {
              width: 50,
              height: 50
            }
          },
          opacity: {
            value: 0.8,
            random: false,
            anim: {
              enable: false,
              speed: 1,
              opacity_min: 0.1,
              sync: false
            }
          },
          size: {
            value: 3,
            random: true,
            anim: {
              enable: true,
              speed: 1,
              size_min: 0.1,
              sync: false
            }
          },
          line_linked: {
            enable: true,
            distance: 100,
            color: "#ffffff",
            opacity: 0.4,
            width: 1
          },
          move: {
            enable: true,
            speed: 6,
            direction: "none",
            random: false,
            straight: false,
            out_mode: "out",
            attract: {
              enable: false,
              rotateX: 600,
              rotateY: 1400
            }
          }
        },
        interactivity: {
          detect_on: "canvas",
          events: {
            onhover: {
              enable: false,
              mode: "repulse"
            },
            onclick: {
              enable: false,
              mode: "push"
            },
            resize: true
          },
          modes: {
            grab: {
              distance: 400,
              line_linked: {
                opacity: 1
              }
            },
            bubble: {
              distance: 400,
              size: 40,
              duration: 2,
              opacity: 8,
              speed: 3
            },
            repulse: {
              distance: 200
            },
            push: {
              particles_nb: 4
            },
            remove: {
              particles_nb: 2
            }
          }
        },
        retina_detect: true,
        config_demo: {
          hide_card: false,
          background_color: "#b61924",
          background_image: "",
          background_position: "50% 50%",
          background_repeat: "no-repeat",
          background_size: "cover"
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
#particles-js {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(60, 40, 95, 0.3215686274509804);
  z-index: -99999;
}
</style>


